import Notification from "./components/Notification";
import React from "react";
import StyledApp from "./styles/app.styled";
import TodoForm from "./components/TodoForm";
import Todos from "./components/Todos";
import server from "./services/server";

export default function App() {
  const [task, setTask] = React.useState([]);
  const [newTask, setNewTask] = React.useState("");
  const [message, setMessage] = React.useState("");
  const [messageType, setMessageType] = React.useState("");

  React.useEffect(() => {
    server.get().then((response) => {
      setTask(response);
    });
  }, []);

  const onChange = (event) => {
    setNewTask(event.target.value);
  };

  const createTask = (event) => {
    event.preventDefault();

    const new_task = {
      task: newTask,
    };

    const exits = task.find(
      (tsk) => tsk.task.toLowerCase() === newTask.toLowerCase()
    );

    if (!exits) {
      server.create(new_task).then((response) => {
        setTask(task.concat(response));
        setNewTask("");

        setMessageType("success");
        setMessage("Task added!");
        setTimeout(() => {
          setMessage(null);
        }, 5000);
      });
    } else {
      setMessageType("error");
      setMessage("Duplicate tasks not allowed!");
      setTimeout(() => {
        setMessage(null);
      }, 5000);
    }
  };

  const deleteTask = (id, tsk) => {
    server
      .remove(id)
      .then((response) => {
        if (response.status === 204) {
          setTask(task.filter((tsk) => tsk.id !== id));
          setMessageType("success");
          setMessage(`Task deleted: ${tsk}!`);
          setTimeout(() => {
            setMessage(null);
          }, 5000);
        }
      })
      .catch((error) => {
        setMessageType("error");
        setMessage("Task already deleted!");
        setTimeout(() => {
          setMessage(null);
        }, 5000);
        setTask(task.filter((tsk) => tsk.id !== id));
      });
  };

  // const editTask = (id, tsk) => {
  //   task.find((tsk) => tsk.id === id) ? setNewTask(tsk) : console.log();
  //   console.log(task.find((tsk) => tsk.id === id));
  // };

  // const updateTask = () => {};

  return (
    <StyledApp>
      <Notification messageType={messageType} message={message} />
      <TodoForm addTask={createTask} onChange={onChange} newTask={newTask} />
      <Todos todos={task} deleteTask={deleteTask} />
    </StyledApp>
  );
}
