import React from "react";
import StyledTodos from "../styles/todos.styled";
import edit from "../icons/edit-task.svg";
import trash from "../icons/trash.svg";

export default function Todos({ todos, deleteTask, editTask }) {
  const size = 16;

  return (
    <StyledTodos>
      {todos.map((todo) => (
        <div key={todo.id} className="todos">
          <p>{todo.task}</p>
          <div className="buttonSet">
            {/* <button
              className="editBtn"
              onClick={() => editTask(todo.id, todo.task)}
            >
              <img src={edit} width={size} height={size} alt="edit" />
            </button> */}
            <button
              className="deleteBtn"
              onClick={() => deleteTask(todo.id, todo.task)}
            >
              <img src={trash} width={size} height={size} alt="trash" />
            </button>
          </div>
        </div>
      ))}
    </StyledTodos>
  );
}
